import { Component } from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import { FdService} from "./fd.service";
import {UtilService} from "../../service/util-service/util.service";
import {FsData} from "../../data.mapping";

/**
 * Generated class for the  page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-fd',
  template:  `
    <modal-box title="{{fd.ran}}" [buttons]="buttons" (onSave)="save()" (onCancel)="dismiss()">      

      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end > {{fd.ran}}</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end > {{fd.rn}}</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >{{fd.rc}}</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end *ngIf="fd.rel == 0"></ion-label>
        <ion-label item-end text-end *ngIf="fd.rel == 1"></ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >150</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >250</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >30</ion-label>
      </div>
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >2</ion-label>
      </div>
      
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-label item-end text-end >{{fd.src}}</ion-label>
      </div>
      
      <div ion-item no-border no-padding no-lines no-margin class="itemwarp font-normal">
        <ion-label></ion-label>
        <ion-toggle [(ngModel)]="fd.isbla" (ionChange)="black(fd.isbla)"></ion-toggle>
      </div>
    </modal-box>
  `,
})
export class FdPage {


  buttons: any = {
    cancel: true
  };

  fd:FsData = new FsData();
  pwi:string;
  buttonText:string = '';
  constructor(public navCtrl: NavController,
              public fdService: FdService,
              public viewCtrl: ViewController,
              private util : UtilService,
              public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FdPage');
    this.fd = this.navParams.get('fsData');
  }

  ionViewDidEnter(){
   this.getDetail();
  }

  getDetail(){
    this.fdService.get(this.fd).then(data=>{
      if(data){
        this.fd = data;
        if(!this.fd.ran || this.fd.ran == ''){
          this.fd.ran = this.fd.rn;
        }
      }
      return this.fdService.getBlack(this.fd.rc);
    }).then( data=>{
      this.fd.isbla = data;

    })
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  black(b:boolean){
    if(!b){
      this.util.alterStart("",()=>{
        this.rbl();
      });

    }else{
      this.util.alterStart("",()=>{
        this.abl();
      });

    }
  }

  /**
   * 
   */
  rbl(){
    this.fdService.removeBlack(this.fd.rc).then(data=>{
        this.getDetail();
        //alert("")
    })
  }

  /**
   * 
   */
  abl(){
    this.fdService.putBlack(this.fd).then(data=>{
        this.getDetail();
        //alert("")
    })
  }
}



